<template>
	<view class="content">
		<view class="name">
			{{company.name}}
		</view>
		<view class="componey">
			{{company.companyName}}
		</view>
		<view class="addr">
			{{company.province==company.city?'':company.province}}{{company.city}}{{company.detailAddr}}
		</view>
		<view class="phone">
		<text style="display: flex;justify-content: flex-end; flex-direction: column; min-width: 200rpx; ">{{phone?phone:"**********"}}</text>
		<view type="primary" size="mini" style="background-color: #007AFF; min-width: 100rpx; text-align: center;border-radius: 20rpx; padding: 10rpx;" @click="looks">复制号码</view>
		<view type="primary" size="mini" style="margin-left: 30rpx; background-color: #4CD964; width: 100rpx; text-align: center;border-radius: 20rpx;padding: 10rpx;" @click="look">查看</view>
		</view>
		<view class="wxchat">
			<text style="display: flex;justify-content: flex-end; flex-direction: column; min-width: 200rpx;">{{company.wxNumber}}</text>
			<view type="primary" size="mini" style="background-color: #007AFF; min-width: 100rpx; text-align: center;border-radius: 20rpx; padding: 10rpx;" @click="lookss">复制微信</view>
		</view>
		<view class="job">
			{{company.serviceScope}}
		</view>
		<view class="" style="position:absolute;bottom: 15%; width: 100%; text-align: center; color: red;">
			联系客服,获取更多查看次数
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				company: '', //公司信息
				flag: false,
				companyId: '',
				list: uni.getStorageSync('ls_my_list') || [],
				phone:''
			}
		},
		created() {

		},
		methods: {
			getinfo(companyId) {
				this.$http.postRequest('/company/query', {
					companyId: companyId
				}).then(res => {
					this.company = res[0];
					if (this.company.isLook == 1) {
						this.flag = true
						return false
					}
				})
			},
			looks() {
				if(this.phone) {
					wx.setClipboardData({
						data:this.phone
					})
				}else {
					uni.showToast({
						title:"请先查看号码",
						icon:'none'
					})
				}
			},
			lookss() {
				wx.setClipboardData({
					 data:this.company.wxNumber
				})
			},
			look() {
				var userid = uni.getStorageSync('userId')
				this.$http.postRequest('/user/queryCountAndTime', {
					userId: userid.userId
				}).then(res => {
					var time1 = new Date(`${res.msg.payTimeStr}`);
					var time2 = new Date();
					if (time1.getTime() <= time2.getTime()) {
						uni.showToast({
							title: "会员已到期",
							icon: 'none'
						})

						return false
					} //到期时间
					if (res.msg.browseCount == 0) {
						uni.showToast({
							title: "剩余次数不足",
							icon: 'none'
						})
						return false
					} //剩余次数
					var userid = uni.getStorageSync('userId')
					this.$http.postRequest('/company/queryByPhone', {
						companyId: this.companyId,
						userId: userid.userId
					}).then(res => {
						console.log(res)
						this.phone=res.phone
						this.flag = true
					}) //查看电话
				})

			}
		},
		onLoad(options) {
			console.log(options)
			this.companyId = options.companyId
			this.getinfo(options.companyId)
		}
	}
</script>
<style>
	page {
		height: 100%;
		width: 100%;
		background-size: 100% 100%;
		background-image: url(data:image/png;base64,);
	}
</style>

<style scoped lang="scss">
	.content{
		font-size: 32rpx;
		color: #FFFFFF;
		// position: relative;
		height: 100%;
		width: 100%;
	}
	.name {
		font-size: 60rpx;
		color: #FFE31F;
		position: fixed;
		top:15%;
		left:14%;
		width: 80%;
	}
	.componey{
		font-size: 40rpx;
		color: #FFFFFF;
		position: fixed;
		top: 22%;
		left:14%;
		width: 80%;
	}
	.job{
		position:fixed;
		top: 70.8%;
		left:38%;
		width: 43%;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	.wxchat{
		position:fixed;
		left:28%;
		top: 58.5%;
		text-align: left;
		box-sizing: border-box;
		display: flex;
		justify-content: space-between;
		// width: 400rpx;
		white-space: nowrap;
	}
	.phone{
		position: fixed;
		top: 48.2%;
		left:28%;
		display: flex;
		justify-content: space-between;
		text-align: left;
		box-sizing: border-box;
		// width: 320rpx;
		white-space: nowrap;
	}
	.addr {
		position: fixed;
		top: 40.5%;
		left:28%;
		text-align: left;
		box-sizing: border-box;
		width: 400rpx;
		white-space: nowrap;
	}
</style>
